import React, { Component } from "react";
import NewsItem from "./NewsItem";
import Spinner from "./Spinner";
import PropTypes from "prop-types";

export class News extends Component {
  static defaultProps = {
    country: "in",
    pageSize: 3,
    category: "general",
  };

  static propTypes = {
    country: PropTypes.string,
    pageSize: PropTypes.number,
    category: PropTypes.string,
  };

  constructor(props) {
    super(props);
    this.state = {
      articles: [],
      loading: true,
      page: 1,
      totalResults: 0
    };
    document.title = `${this.props.category} - News24Seven`;
  }

  async updateNews() {
    const url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=${this.props.apiKey}&page=${this.state.page}&pageSize=${this.props.pageSize}`;
    this.setState({ loading: true });
    let data = await fetch(url);
    let parsedData = await data.json();
    console.log(parsedData);
    this.setState({
      articles: parsedData.articles,
      totalResults: parsedData.totalResults,
      loading: false
    });
  }

  async componentDidMount() {
    this.updateNews();
  }

  fetchMoreData = async () => {
    this.setState({page : this.state.page + 1})
    const url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=52a4ac0385e44e6a826a84e48eed1ff8&page=1&pageSize=${this.props.pageSize}`;
    let data = await fetch(url);
    let parsedData = await data.json();
    this.setState({
      articles: this.state.articles.concat(parsedData.articles),
      totalResults: parsedData.totalResults 
    });
  };

  handlePrevClick = async () => {
    this.updateNews();
    this.setState({
      page: this.state.page - 1
    });
  };

  handleNextClick = async () => {
    this.updateNews();
    this.setState({
      page: this.state.page + 1
    });
  };

  render() {
    return (
      <>
        <h2 className="text-center headline">{`${this.props.category} `}</h2>
        {this.state.loading && <Spinner/>}
     
          <div className="container">
          <div className="row my-3">
            {this.state.articles.map((element) => {
              return (
                <div className="col-md-4" key={element.url}>
                  <NewsItem
                    title={element.title}
                    description={element.description}
                    imageUrl={element.urlToImage}
                    newsUrl={element.url}
                    author={element.author}
                    date={element.publishedAt}
                    source={element.source.name}
                  />
                </div>
              );
            })}
            </div>
            <div className = "container d-flex justify-content-between">
            <button disabled = {this.state.page <= 1} type = "button" className="btn btn-sm btn-primary" onClick = {this.handlePrevClick}>
              Previous
            </button>
            <button disabled = {this.state.page > Math.ceil(this.state.totalResults / this.props.pageSize)} type = "button" className="btn btn-sm btn-primary" onClick = {this.handleNextClick}>
              Next
            </button>
            </div>
          </div>
        
        
      </>
    );
  }
}

export default News;
